import { WebRtcPeer } from 'kurento-utils-browser';
import { ElementRef } from '@angular/core';
import { EventService } from './event.service';
import { C3event } from '../models/c3event';
import { EventType } from '../models/event-type.enum';

export class KurentoService {

  private webRtcPeer: WebRtcPeer;

  /**
   * Creates a new Kurento Service
   * @param ws WEb Socket Connection
   * @param audioSource Audio URL
   */
  constructor(private ws: WebSocket, private audioSource: string, private video: ElementRef, private eventService: EventService) {
    
  }

  /**
   * Starts playing the streamed audio
   */
  start(millis: number): void{
    console.log("RTC service: Playing audio at initial position: "+millis);
    let userMediaConstraints = {
      audio: true,
      video: false
    }
    let options = {
      remoteVideo: this.video.nativeElement,
      mediaConstraints: userMediaConstraints,
      onicecandidate: (candidate: any) => {
        console.log('RTC service: Local candidate ' + JSON.stringify(candidate));
        let message = {
          id: 'onIceCandidate',
          candidate: candidate
        }
        try{
          this.sendMessage(message);
        }catch(error){
          let event : C3event = {type: EventType.WebSocketFailed, value: {reason:error}};
          this.eventService.Emitter.next(event);
        }
      }
    }
    this.webRtcPeer = WebRtcPeer.WebRtcPeerRecvonly(options, (error: String) => {
      if(error){
        console.error("Error creating the peer connection: %s", error);
        let event : C3event = {type: EventType.RemotePlayFailed, value: {at:"start", reason:error}};
        this.eventService.Emitter.next(event);
      }else{
        this.webRtcPeer.generateOffer((error: String, sdp: String) => {
          if(error){
            console.error("Error generating the offer: %s", error);
            let event : C3event = {type: EventType.RemotePlayFailed, value: {at:"start", reason:error}};
            this.eventService.Emitter.next(event);
          }else{
            let message = {
              id: 'start',
              sdpOffer: sdp,
              position: millis,
              videourl: this.audioSource
            }
            console.log("Sending start message");
            try{
              this.sendMessage(message);
            }catch(error){
              let event : C3event = {type: EventType.WebSocketFailed, value: {reason:error}};
              this.eventService.Emitter.next(event);
            }
          }
        });
      }
    });
  }

  /**
   * Stops the streaming
   */
  stop(): void{
    console.log("RTC service: Stopping audio");
    if (this.webRtcPeer) {
      try{
        this.webRtcPeer.dispose();
        this.webRtcPeer = null;
        var message = {
            id: 'stop'
        }
        this.sendMessage(message);
        let event : C3event = {type: EventType.RemotePlayStopped, value: {}};
        this.eventService.Emitter.next(event);
      }catch(error){
        console.error("Error stopping the player: %s", error);
        let event : C3event = {type: EventType.RemotePlayFailed, value: {at:"stop", reason:error}};
        this.eventService.Emitter.next(event);
      }
    }else{
      console.error("Error stopping the player: peer connection is closed");
      let event : C3event = {type: EventType.RemotePlayFailed, value: {at:"stop", reason:"Peer connection is closed"}};
      this.eventService.Emitter.next(event);
    }
  }

  /**
   * Resets the Peer connection
   */
  resetConnection(): void{
    console.log("RTC service: resetting peer connection");
    if(this.webRtcPeer){
      try{
        this.webRtcPeer.dispose();
        this.webRtcPeer = null;
        let event : C3event = {type: EventType.RemoteConnectionReset, value: {}};
        this.eventService.Emitter.next(event);
      }catch(error){
        console.error("Error resetting the peer connection: %s", error);
        let event : C3event = {type: EventType.LocalConnectionError, value: {at: "resetConnection", reason:error}};
        this.eventService.Emitter.next(event);
      }
    }
  }

  /**
   * Moves to the specified millisecond
   * @param time: played time in milliseconds
   */
  doSeekAt(time: number): void{
    console.log("RTC service: doing seek at %s", time.toString());
    try{
      var message = {
        id: 'doSeek',
        position: time
      }
      this.sendMessage(message);
      let event : C3event = {type: EventType.RemotePlaySeeked, value: {seekTime: time}};
      this.eventService.Emitter.next(event);
    }catch(error){
      console.error("Error doing seek: %s", error);
      let event : C3event = {type: EventType.RemoteSeekFailed, value: {at: "doSeek", reason:error}};
      this.eventService.Emitter.next(event);
    }
  }

  /**
   * Pauses the streaming
   */
  pause(): void{
    console.log("RTC service: Pausing audio");
    try{
      var message = {
        id: 'pause'
      }
      this.sendMessage(message);
      let event : C3event = {type: EventType.RemotePlayPaused, value: {}};
      this.eventService.Emitter.next(event);
    }catch(error){
      console.error("Error pausing the streaming: %s", error);
      let event : C3event = {type: EventType.RemotePlayFailed, value: {at: "pause", reason:error}};
      this.eventService.Emitter.next(event);
    }
  }

  /**
   * Resumes the streaming
   */
  resume(): void{
    console.log("RTC service: Resuming audio");
    try{
      var message = {
        id: 'resume'
      }
      this.sendMessage(message);
      let event : C3event = {type: EventType.RemotePlayResumed, value: {}};
      this.eventService.Emitter.next(event);
    }catch(error){
      console.error("Error resuming the streaming: %s", error);
      let event : C3event = {type: EventType.WebSocketFailed, value: {reason:error}};
      this.eventService.Emitter.next(event);
    }
  }

  mute(): void{
    if(this.webRtcPeer){
      try{
        this.webRtcPeer.getRemoteStream().getAudioTracks().forEach(track => {
          track.enabled = false;
        });
        let event : C3event = {type: EventType.LocalAudioLevel, value: {action:"mute"}};
        this.eventService.Emitter.next(event);
      }catch(error){
        console.error("Error muting audio: %s", error);
        let event : C3event = {type: EventType.LocalAudioLevelError, value: {at: "mute", reason:error}};
        this.eventService.Emitter.next(event);
      }
    }else{
      let event : C3event = {type: EventType.LocalAudioLevel, value: {action:"mute"}};
      this.eventService.Emitter.next(event);
    }
  }

  unmute(): void{
    if(this.webRtcPeer){
      try{
        this.webRtcPeer.getRemoteStream().getAudioTracks().forEach(track => {
          track.enabled = true;
        });
        let event : C3event = {type: EventType.LocalAudioLevel, value: {action:"unmute"}};
        this.eventService.Emitter.next(event);
      }catch(error){
        console.error("Error unmuting audio: %s", error);
        let event : C3event = {type: EventType.LocalAudioLevelError, value: {at: "unmute", reason:error}};
        this.eventService.Emitter.next(event);
      }
    }else{
      let event : C3event = {type: EventType.LocalAudioLevel, value: {action:"unmute"}};
      this.eventService.Emitter.next(event);
    }
  }

  /**
   * Processes the SDP answer
   * @param sdpAnswer SDP answer as string
   * @param callback callback
   */
  processAnswer(sdpAnswer: string, callback: (error: string) => void) :void{
    console.log("RTC service: Processing SDP answer: %s", sdpAnswer);
    this.webRtcPeer.processAnswer(sdpAnswer, callback);
  }

  /**
   * Adds ICE candidate
   * @param candidate candidate
   * @param callback callback
   */
  addIceCandidate(candidate: RTCIceCandidate, callback: (error: string) => void): void{
    console.log("RTC service: Adding Ice Candidate: %s", JSON.stringify(candidate));
    this.webRtcPeer.addIceCandidate(candidate, callback);
  }

  sendMessage(message: object): void{
    if(this.ws.readyState == 1){
      this.ws.send(JSON.stringify(message));
    }else{
      throw new Error("Websocket is closed");
    }
  }
}
