import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { C3event } from '../models/c3event';

@Injectable({
  providedIn: 'root'
})
export class EventService {

  private emitter: BehaviorSubject<C3event>;
  constructor() {
    this.emitter = new BehaviorSubject<C3event>(null);
  }

  get Emitter(): BehaviorSubject<C3event>{
    return this.emitter;
  }
}
